// components/ServicePage.tsx
"use client";
import { ReactNode } from "react";
import { motion } from "framer-motion";

type Bullet = { icon?: ReactNode; text: string };
type FAQ = { q: string; a: string };

export type ServicePageProps = {
  title: string;
  subtitle?: string;
  intro: string;
  bullets?: Bullet[];
  heroImage?: string; // /public/...
  sections?: Array<{
    id: string;
    title: string;
    content: ReactNode;
  }>;
  cta?: {
    title: string;
    text: string;
    buttonText: string;
    href: string; // WhatsApp o /contacto
  };
  faqs?: FAQ[];
};

export default function ServicePage({
  title,
  subtitle,
  intro,
  bullets = [],
  heroImage,
  sections = [],
  cta,
  faqs = [],
}: ServicePageProps) {
  return (
    <div className="relative">
      {/* Hero */}
      <section className="relative overflow-hidden">
        <div className="mx-auto max-w-6xl px-4 py-16 md:py-24">
          <div className="grid gap-10 md:grid-cols-2 md:items-center">
            <div>
              <p className="text-xs tracking-[0.2em] text-[#D4AF37]/80">
                SERVICIO
              </p>
              <h1 className="mt-2 text-3xl font-bold text-white md:text-5xl">
                {title}
              </h1>
              {subtitle && (
                <p className="mt-2 text-lg text-neutral-300">{subtitle}</p>
              )}
              <p className="mt-6 text-neutral-300">{intro}</p>

              {bullets.length > 0 && (
                <ul className="mt-6 space-y-2">
                  {bullets.map((b, i) => (
                    <li key={i} className="flex items-start gap-3">
                      <span className="mt-1 inline-block h-2 w-2 rounded-full bg-[#D4AF37]" />
                      <span className="text-neutral-300">{b.text}</span>
                    </li>
                  ))}
                </ul>
              )}
            </div>

            {heroImage && (
              <motion.div
                initial={{ opacity: 0, y: 24 }}
                whileInView={{ opacity: 1, y: 0 }}
                viewport={{ once: true, amount: 0.4 }}
                transition={{ duration: 0.6, ease: "easeOut" }}
                className="relative aspect-[5/4] overflow-hidden rounded-3xl border border-white/10 bg-white/5"
              >
                <img
                  src={heroImage}
                  alt={title}
                  className="h-full w-full object-cover"
                />
                <div className="pointer-events-none absolute inset-0 bg-gradient-to-tr from-black/40 to-transparent" />
              </motion.div>
            )}
          </div>
        </div>
      </section>

      {/* Sections */}
      {sections.length > 0 && (
        <section className="relative">
          <div className="mx-auto max-w-6xl px-4 py-10 md:py-16">
            <div className="grid gap-8">
              {sections.map((s) => (
                <div id={s.id} key={s.id} className="rounded-2xl border border-white/10 bg-white/5 p-6">
                  <h2 className="text-xl font-semibold text-white md:text-2xl">
                    {s.title}
                  </h2>
                  <div className="prose prose-invert mt-4 max-w-none">
                    {s.content}
                  </div>
                </div>
              ))}
            </div>
          </div>
        </section>
      )}

      {/* CTA */}
      {cta && (
        <section className="relative">
          <div className="mx-auto max-w-6xl px-4 pb-16 pt-4 md:pb-24">
            <div className="overflow-hidden rounded-2xl border border-[#D4AF37]/30 bg-gradient-to-b from-[#D4AF37]/10 to-transparent p-6 md:p-10">
              <h3 className="text-2xl font-bold text-white md:text-3xl">
                {cta.title}
              </h3>
              <p className="mt-2 max-w-3xl text-neutral-300">{cta.text}</p>
              <a
                href={cta.href}
                className="mt-6 inline-flex rounded-xl border border-[#D4AF37] px-5 py-2.5 text-sm font-medium text-[#D4AF37] transition hover:bg-[#D4AF37] hover:text-[#242424]"
              >
                {cta.buttonText}
              </a>
            </div>
          </div>
        </section>
      )}

      {/* FAQs */}
      {faqs.length > 0 && (
        <section className="relative">
          <div className="mx-auto max-w-6xl px-4 pb-20">
            <h3 className="text-2xl font-semibold text-white">Preguntas frecuentes</h3>
            <div className="mt-6 grid gap-4 md:grid-cols-2">
              {faqs.map((f, i) => (
                <details
                  key={i}
                  className="rounded-xl border border-white/10 bg-white/5 p-4 open:bg-white/10"
                >
                  <summary className="cursor-pointer text-white">{f.q}</summary>
                  <p className="mt-2 text-neutral-300">{f.a}</p>
                </details>
              ))}
            </div>
          </div>
        </section>
      )}
    </div>
  );
}
